import { component, mixin, createCell, Fragment } from 'web-cell';
import { observer } from 'mobx-web-cell';
import { Jumbotron } from 'boot-cell/source/Content/Jumbotron';
import { Button } from 'boot-cell/source/Form/Button';
import { Image } from 'boot-cell/source/Media/Image';
import { SpinnerBox } from 'boot-cell/source/Prompt/Spinner';
import { Card, CardHeader } from 'boot-cell/source/Content/Card';
import { Badge } from 'boot-cell/source/Reminder/Badge';

import companies from '../data/members-china.json';
import { member, proposal, Member, Proposal } from '../model';

const thisYear = new Date().getFullYear();

@observer
@component({
  tagName: 'main-page',
  renderTarget: 'children',
})
export class MainPage extends mixin() {
  connectedCallback() {
    if (member.list.length < 1) member.getList();
    if (proposal.finishedList.length < 1) proposal.getList();

    super.connectedCallback();
  }

  renderMember = ({
    avatar_url,
    username,
    url,
    name,
    company,
    location,
    bio,
  }: Member) => {
    return (
      <div className="col-12 col-sm-6 col-md-3 my-3">
        <Card
          image={avatar_url}
          title={
            <a
              className="stretched-link"
              title={username}
              target="_blank"
              href={url}
            >
              {name}
            </a>
          }
        >
          <dl>
            <dt></dt>
            <dd>{company}</dd>
            <dt></dt>
            <dd>{location}</dd>
            <dt></dt>
            <dd>{bio}</dd>
          </dl>
        </Card>
      </div>
    );
  };

  renderProposal = ({
    link,
    name,
    tags,
    edition,
    authors,
    champions,
  }: Proposal) => {
    const standard = tags[0].split('-')[1];

    return (
      <div className="col-12 col-sm-6 col-md-3 my-3">
        <Card
          className="h-100"
          title={
            link ? (
              <a target="_blank" href={link}>
                {name}
              </a>
            ) : (
              name
            )
          }
        >
          <CardHeader className="d-flex justify-content-around">
            <Badge
              color="primary"
              target="_blank"
              href={`https://tc39.es/ecma${standard}/`}
            >
              ECMA-{standard}
            </Badge>
            {standard === '262' && edition <= thisYear ? (
              <Badge
                color="success"
                target="_blank"
                href={`https://www.ecma-international.org/ecma-262/${
                  edition - 2009
                }.0/`}
              >
                ES {edition}
              </Badge>
            ) : null}
          </CardHeader>
          {(authors || champions) && (
            <dl>
              {authors && (
                <>
                  <dt></dt>
                  <dd>{authors.join(', ')}</dd>
                </>
              )}
              {champions && (
                <>
                  <dt></dt>
                  <dd>{champions.join(', ')}</dd>
                </>
              )}
            </dl>
          )}
        </Card>
      </div>
    );
  };

  render() {
    return (
      <>
        <Jumbotron
          className="mt-4 text-center"
          title={
            <span className="text-nowrap">
              JavaScript&nbsp;
              <wbr />
              
            </span>
          }
          description=" JavaScript  JavaScript ECMAScript"
        >
          <p className="mb-4">
            JSCIG  JavaScript  Ecma
             TC39  TC39
             JavaScript
             JavaScript  JavaScript
            
          </p>

          <Button className="mr-3" size="lg" color="success" href="proposals">
            
          </Button>
          <Button
            size="lg"
            target="_blank"
            href="https://github.com/JSCIG/es-discuss/issues"
          >
            
          </Button>
        </Jumbotron>

        <h2 className="mt-4 text-center">TC39 </h2>

        <section className="row">
          {companies.map(({ link, logo, name }) => (
            <a
              className="d-flex col-12 col-sm-4 col-md-3 my-3 justify-content-center align-items-center"
              target="_blank"
              href={link}
            >
              <Image fluid title={name} src={logo} />
            </a>
          ))}
        </section>

        <h2 className="mt-4 text-center">JSCIG </h2>

        <SpinnerBox className="row" cover={member.loading}>
          {member.list.map(this.renderMember)}
        </SpinnerBox>

        <h2 className="mt-4 text-center">TC39 </h2>

        <SpinnerBox className="row" cover={proposal.loading}>
          {proposal.finishedList.map(this.renderProposal)}
        </SpinnerBox>
      </>
    );
  }
}
